:root {
  --bg-color: #eee;
  --text-color: black;
}

html {
  height: 100%;
  background: var(--bg-color);
  color: var(--text-color);
  font-family: sans-serif;
}

body {
  margin: 0;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.title {
  font-size: 4em;
  text-transform: uppercase;
  margin: 0;
}

@media (prefers-color-scheme: dark) {
  body {
    --bg-color: #333;
    --text-color: white;
  }
}
